.pageHeaders {
    text-align: center;
    margin-right: 20px;
}

.panel-default {
    border: none;
}
.pageHeaders h1 {
    font-size: 20px;
    color: #ffffff;
}

.pageHeaders small {
    color: #ffffff;
}

.bursaryPanel {
    height: auto;
    background-color: #2F006F;
    margin: 0;
    padding: 0;
    border-radius: 0;
}
.thumbnail {
    display: block;
    padding: 0px;
    margin-bottom: 20px;
    line-height: 1.42857143;
    background-color: #fff;
    border: none;
    border-radius: 0px;
    -webkit-transition: border .2s ease-in-out;
    transition: border .2s ease-in-out;
    width: 125%;
    margin-left: 55px;
}

.thumbnail img {
    width: 100%
}

.thumbnail .caption {
    padding: 0;
    margin: 0;
};

.thumbnail h3 {
    font-size: 15px;
    margin: 0;
    padding: 5px;
}

.thumbnail p {
    padding: 5px;
}

.thumbnail button {
    width: 100%;
    margin: 0;
    bottom: 0;
    left: 0;
    right: 0;
    position: relative;
    background:#6C7828;
    color: #ffffff;
    text-shadow:#000;
}

@media (min-width: 320px) and (max-width: 480px) {
    .pageHeaders {
        margin-right: 0px;
    }
    
    .thumbnail {
        display: block;
        padding: 0px;
        margin-bottom: 20px;
        line-height: 1.42857143;
        background-color: #fff;
        /* border: 1px solid #ddd; */
        /* border-radius: 4px; */
        -webkit-transition: border .2s ease-in-out;
        transition: border .2s ease-in-out;
        margin-left: 1px;
        width: 105%;
    }

    .thumbnail h3 {
        font-size: 15px;
        margin: 0;
        padding: 5px;
    }

    .thumbnail p {
        padding: 5px;
    }
  }

.panel-default {
    border: none;
}
.aboutHeaders h1 {
    font-size: 20px;
    color:#6C7828;
    text-align: center;
}

.aboutusPanel {
    height: auto;
    background-color: white;
    margin: 0;
    padding: 0;
    border-radius: 0;
}

.aboutTagline {
    text-align: center;
}

.aboutHeading {
    text-align: center;
}

h4 span:nth-child(1) {
    font-size: 20px;
    color: grey;
    font-weight: bold;
    padding-right: 10px;
}

h4 span:nth-child(2) {
    font-size: 20px;
    color: #6C7828;
    font-weight: bold;
    padding-right: 10px;
};
.navbar {
    border-radius: 0px;
    height: 86px;
    padding: 10px;
    background: #2F006F;
    color: #fff;
    margin: 0;
}

.logo {
    width: 146px;
    height: 75px;
    margin: 0;
    padding-top: 3px;
}

.active {
   border-bottom: 2px solid #6C7828;
}

.links {
    padding-top: 11px;
    color: green;
}

@media (min-width: 320px) and (max-width: 480px) {
    .navbar-collapse {
        z-index: 1;
        position: absolute;
        background: #2F006F;
        width: 100%;
        margin: 0;
    }
}
.carousel-image {
    height: 320px;
    width: 100%;
    background-size: cover;
}

.image-overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);
    z-index: 2;
    cursor: pointer;
};

.heading {
    font-size: 55px
}

.subtext {
    font-size: 18px
}

.carouselCaption-container {
    height: 80%;
}
.footerHeading {
    color: #6C7828;
}

h5, p {
    color: lightgray
}

.App {
  height: 100vh;
  width: 100vw;
  background-color: #2f006f;
}

.map {
  height: 500px;
  width: 500px;
  top: 0;
  position: -webkit-sticky;
  position: sticky;
  -webkit-text-fill-color: rgb(90, 88, 88);
}

